        <Nav className="mr-auto">
          <Nav.Link href="#features" className='navigation_item'>Головна</Nav.Link>
          <Nav.Link href="#pricing" className='navigation_item'>Курси</Nav.Link>
        </Nav>
        <Nav>
          <Nav.Link href="#deets" className='navigation_item'>Блог</Nav.Link>
